@import 'src/variables.scss';
.footer{
  position: relative;
  width: 100%;
  height: 50px;
  background-color: $background-color;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.footer a{
  text-decoration:none ;
}
a:hover img{
  background-color: $primary-color;
}
@media screen and (max-width: 1200px) {
  span{
    display: none;
  }
}