@import 'src/variables.scss';

.title{
  width: 100%;
  text-align: center;
}
.contacts {
  width: 100%;
  min-height: 82vh;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.form {
  width: 540px;
  height: 440px;
  background: #fafafa;
  border-radius: 8px;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  margin: calc(10vh - 120px) auto;
  padding: 20px 30px;
  max-width: calc(100vw - 40px);
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  position: relative;
}

.contacts h2 {
  margin: 10px 0;
  padding-bottom: 10px;
  width: 230px;
  color: #78788c;
  border-bottom: 3px solid #78788c
}
.title{
  margin-top: 100px;
}

.contacts input {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background: none;
  outline: none;
  resize: none;
  border: 0;
  font-family: 'Montserrat', sans-serif;
  transition: all .3s;
  border-bottom: 2px solid #bebed2
}

.contacts input:focus {
  border-bottom: 2px solid #78788c
}

.contacts p:before {
  content: attr(type);
  display: block;
  margin: 28px 0 0;
  font-size: 14px;
  color: #5a5a5a
}

.contacts button {
  float: right;
  padding: 8px 12px;
  margin: 28px 0 0;
  font-family: 'Montserrat', sans-serif;
  border: 2px solid #78788c;
  background: $primary-color;
  color: whitesmoke;
  cursor: pointer;
  transition: all .3s
}

.contacts button:hover {
  background: black;
  color: #fff
}

.contacts div {
  content: 'Hi';
  position: absolute;
  bottom: 0;
  right: 0;
  background: gray;
  color: #fff;
  width: 100%;
  padding: 16px 4px 16px 0;
  text-align: center;
  border-radius: 0 0 6px 6px;
  font-size: 15px;
}

.contacts span {
  margin: 0 5px 0 15px
}