.container {
    /*border: 1px solid darkgray;*/
    height: 100%;
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: start;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 1px;
    margin-bottom: 1px;
}

@media screen and (max-width: 1200px) {
    .container {
        width: 100%;
    }

}